import React, { useState, useEffect } from "react";

const ScrollToTopButton = () => {
  //  isFixed  false
  const [isFixed, setIsFixed] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      //  500px  250px 768px  130px
      if (
        window.pageYOffset > 500 &&
        window.innerHeight + window.pageYOffset <
          document.body.offsetHeight - (window.innerWidth < 768 ? 130 : 250)
      ) {
        setIsFixed(true);
      } else {
        setIsFixed(false);
      }
    };
    window.addEventListener("scroll", handleScroll); // 
    return () => window.removeEventListener("scroll", handleScroll); // 
  }, []);

  // 
  const handleClick = () => window.scrollTo({ top: 0, behavior: "smooth" });

  //  CSS style
  const style = {
    position: isFixed ? "fixed" : "absolute",
    //  bottom  100px(fixed)  350px(absolute)
    //  bottom  50px(fixed)  180px(absolute)
    bottom: isFixed
      ? window.innerWidth < 768
        ? "50px"
        : "100px"
      : window.innerWidth < 768
      ? "180px"
      : "350px",
    right: window.innerWidth < 768 ? "8%" : "3%", //  768px right  8% right  3%
  };

  return (
    <button style={style} className="scroll-to-top" onClick={handleClick}>
      <i className="fa fa-arrow-up" aria-hidden="true"></i>
    </button>
  );
};

export default ScrollToTopButton;
