import { useTranslation } from "react-i18next";

import Aos from "aos";
import "aos/dist/aos.css";

import { Layout } from "../Components/Layout";
import { imgHandler } from "../Components/imgHandler";

const data = {
  development: {
    image: imgHandler("/img/sub-development.png"),
    alt: "",
  },
};

Aos.init({
  offset: 100,
  delay: 50,
  once: true,
});

const Development = () => {
  const { t } = useTranslation();

  return (
    <Layout>
      <div className="banner">
        <img src={data.development.image} alt={data.development.alt} />
      </div>

      <div className="development-content">
        <h2 id={"R&D"} data-aos="fade-up" data-aos-duraction="500">
          
        </h2>

        <div className="content-wrap">
          {/*  */}
          <img
            src={imgHandler("/img/WCHP.png")}
            alt=""
            data-aos="fade-right"
          />
          <div
            className="content-txt"
            data-aos="fade-up"
            data-aos-duraction="500"
          >
            <h2>{t("")}</h2>
            <p>
              {t(
                "BMS()BPU()EMS() "
              )}
            </p>
          </div>

          {/*  */}
          <div
            className="content-txt"
            data-aos="fade-up"
            data-aos-duraction="500"
          >
            <h2>{t("")}</h2>
            <p>
              {t(
                ""
              )}
            </p>
          </div>
          <img
            src={imgHandler("/img/ASD.jpg")}
            alt=""
            data-aos="fade-left"
          />

          {/* () */}
          <img
            src={imgHandler("/img/Container.png")}
            alt="()"
            data-aos="fade-right"
          />
          <div
            className="content-txt"
            data-aos="fade-up"
            data-aos-duraction="500"
          >
            <h2>{t("()")}</h2>
            <p>
              {t(
                "/"
              )}
            </p>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Development;
